@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  font-size: 1rem;
}

.App {
  text-align: center;
  font-size: calc(8px + 1.5vmin);
  /*font-family: 'Padauk', sans-serif;*/
  font-family: 'mm_font';
  src: url(/static/media/myanmarcensus.d1007800.woff2) format('woff2'),
    url(/static/media/myanmarcensus.e2e5cd46.woff) format('woff'),
    url(/static/media/myanmarcensus.82cfd74e.ttf) format('truetype');
}

option {
  font-family: 'mm_font'!important;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-body {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: left;
          align-items: left;
  -webkit-justify-content: center;
          justify-content: center;
  text-align: left;
  margin: 20px 50px;
}

.App-link {
  color: #61dafb;
}

input, select {
  font-size: 1rem;
}

.picker {
  margin-top: 10px;
}

.pickerWrapper {
  margin: 10px 0px;
  width: 150px;
}

.QuestionText {
  font-weight: bold;
  font-size: 1.2rem;
}

label {
  font-size: 1.1rem;
}

.submitButton {
  background-color: #444;
  color: #eee;
  height: 60px;
}

.submitButton:disabled{
  background-color: #aaa;
  color: #eee;
}

.downloadLink{
  font-size: .7em;
  margin-top: 10px;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.BarchartQuestion .BarchartQuestionItem{padding:5px 10px;line-height:1.5em}.BarchartQuestion .BarchartQuestionItemButtons{text-align:right;display:inline-block;padding:0px 10px;vertical-align:top}.BarchartQuestion .changeValueButton{background-color:#444;color:#eee;font-size:1.2rem;border:none}.BarchartQuestion .changeValueButton:disabled{background-color:#aaa;color:#eee}.BarchartQuestion .barChartBarLabel{font-size:0.8em;margin-left:5px}.BarchartQuestion .count{text-anchor:end}.BarchartQuestion .barChartBar{fill:#4fb890}.BarchartQuestion .notSelected{fill:#dddddd}

.FreeTextQuestion input{height:30px;width:300px;border:1px solid #ccc}

.OptionsQuestion select{min-width:300px;max-width:300px;height:30px;border:1px solid #ccc}

